// Componente de Ranking

import { apiService } from '@/services/api.js';
import { stateManager } from '@/services/state.js';
import { escapeHtml } from '@/utils/helpers.js';

export class RankingComponent {
  constructor(container) {
    this.container = container;
    this.ranking = [];
    this.render();
    this.loadRanking();
  }

  render() {
    this.container.innerHTML = `
      <div id="ranking-list" class="ranking-list">
        <div class="loading">Carregando ranking...</div>
      </div>
    `;
  }

  async loadRanking() {
    const rankingList = this.container.querySelector('#ranking-list');
    
    try {
      this.ranking = await apiService.getRanking();
      this.renderRanking();
    } catch (error) {
      console.error('Load ranking error:', error);
      rankingList.innerHTML = '<div class="error">Erro ao carregar ranking.</div>';
    }
  }

  renderRanking() {
    const rankingList = this.container.querySelector('#ranking-list');
    const state = stateManager.getState();
    const currentUser = state.user?.nome;
    const isAdmin = state.userType === 'Administrador';

    if (this.ranking.length === 0) {
      rankingList.innerHTML = '<div class="empty">Nenhum usu√°rio encontrado no ranking.</div>';
      return;
    }

    rankingList.innerHTML = this.ranking.map((userRank, index) => {
      const isCurrentUser = userRank.nome === currentUser && !isAdmin;
      const position = index + 1;
      
      let badge = '';
      let positionClass = '';
      
      if (position === 1) {
        badge = '<span class="ranking-badge gold">ü•á</span>';
        positionClass = 'top-1';
      } else if (position === 2) {
        badge = '<span class="ranking-badge silver">ü•à</span>';
        positionClass = 'top-2';
      } else if (position === 3) {
        badge = '<span class="ranking-badge bronze">ü•â</span>';
        positionClass = 'top-3';
      }

      return `
        <div class="ranking-item ${positionClass} ${isCurrentUser ? 'current-user' : ''}">
          <div class="ranking-position">#${position}</div>
          <div class="ranking-name">
            ${escapeHtml(userRank.nome)}${isCurrentUser ? ' (Voc√™)' : ''}
          </div>
          <div class="ranking-points">${userRank.pontos} pts</div>
          ${badge}
        </div>
      `;
    }).join('');
  }

  refresh() {
    this.loadRanking();
  }
}
